___CSS_LOADER_EXPORT___.push([module.id, `.settings-modal {
  width: 550px;
  max-width: 90vw;
  max-height: 80vh;
  display: flex;
  flex-direction: column;
}

.settings-modal .modal-body {
  overflow-y: auto;
  flex: 1;
  min-height: 300px;
}

.settings-tabs {
  display: flex;
  border-bottom: 1px solid var(--border-color);
  padding: 0 20px;
  gap: 4px;
}

.settings-tab {
  padding: 10px 16px;
  font-size: 13px;
  font-weight: 500;
  color: var(--text-secondary);
  background: transparent;
  border: none;
  border-bottom: 2px solid transparent;
  cursor: pointer;
  transition: all var(--transition);
  margin-bottom: -1px;
}

.settings-tab:hover {
  color: var(--text-primary);
  background: rgba(255, 255, 255, 0.03);
}

.settings-tab.active {
  color: var(--accent-primary);
  border-bottom-color: var(--accent-primary);
}

.settings-section {
  padding: 8px 0;
}

.settings-section-title {
  font-size: 14px;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0 0 16px 0;
}

.settings-row {
  display: flex;
  gap: 16px;
}

.settings-row .input-group {
  flex: 1;
}

.folder-input-row {
  display: flex;
  gap: 8px;
}

.folder-input-row .input {
  flex: 1;
}

.btn-danger-subtle {
  color: var(--error-light);
  background: transparent;
  border-color: transparent;
}

.btn-danger-subtle:hover {
  background: rgba(255, 68, 68, 0.1);
  border-color: rgba(255, 68, 68, 0.3);
}

.input-hint {
  font-size: 11px;
  color: var(--text-tertiary);
  margin-top: 4px;
}
