___CSS_LOADER_EXPORT___.push([module.id, `.file-browser {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.file-browser-header {
  padding: 16px;
  border-bottom: 1px solid var(--border-color);
  display: flex;
  gap: 8px;
}

.file-browser-header .btn-primary {
  flex: 1;
  justify-content: center;
}

.file-browser-error {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 16px;
  background: rgba(255, 68, 68, 0.1);
  color: var(--error);
  font-size: 12px;
}

.file-browser-path {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 8px 12px;
  background: rgba(255, 255, 255, 0.03);
  border-bottom: 1px solid var(--border-color);
  font-size: 12px;
  color: var(--text-secondary);
}

.file-browser-path span {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.file-browser-content {
  flex: 1;
  overflow: auto;
  min-height: 0;
}

.file-tree-container {
  padding: 8px;
}

.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px 20px;
  text-align: center;
}

.empty-state p {
  margin: 8px 0 0;
  color: var(--text-tertiary);
}

.empty-state .text-sm {
  margin-top: 4px;
}

.scanning-indicator {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
  padding: 40px 20px;
  color: var(--text-tertiary);
}

.loading-spinner {
  width: 24px;
  height: 24px;
  border: 2px solid rgba(255, 255, 255, 0.1);
  border-top-color: var(--accent-primary);
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.file-browser-preview {
  flex-shrink: 0;
  border-top: 1px solid var(--border-color);
  max-height: 300px;
  overflow: auto;
}

.file-tree {
  display: flex;
  flex-direction: column;
  gap: 2px;
}
