  height: 100%;
  display: flex;
  flex-direction: column;
}

.channels-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
}

.channels-title {
  font-size: 20px;
  font-weight: 600;
  color: var(--text-primary);
}

.channels-grid {
  display: flex;
  gap: 20px;
  overflow-x: auto;
  padding-bottom: 20px;
  flex: 1;
}

.empty-state {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 16px;
  text-align: center;
  color: var(--text-tertiary);
}

.empty-state h3 {
  font-size: 18px;
  font-weight: 600;
  color: var(--text-secondary);
  margin: 0;
}

.empty-state p {
  font-size: 14px;
  margin: 0;
