___CSS_LOADER_EXPORT___.push([module.id, `.macro-editor {
  width: 600px;
  max-width: 90vw;
  max-height: 80vh;
  display: flex;
  flex-direction: column;
}

.macro-editor .modal-body {
  overflow-y: auto;
  flex: 1;
}

.macro-form {
  margin-bottom: 20px;
}

.input-row {
  display: flex;
  gap: 16px;
  align-items: flex-end;
}

.input-color {
  width: 60px;
  height: 40px;
  padding: 4px;
  cursor: pointer;
}

.checkbox-label {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 13px;
  color: var(--text-secondary);
  cursor: pointer;
  height: 40px;
}

.checkbox-label input[type="checkbox"] {
  width: 16px;
  height: 16px;
  cursor: pointer;
}

.commands-section {
  border-top: 1px solid var(--border-color);
  padding-top: 16px;
}

.commands-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
}

.commands-header h3 {
  margin: 0;
  font-size: 14px;
  font-weight: 600;
  color: var(--text-primary);
}

.btn-sm {
  padding: 6px 12px;
  font-size: 12px;
}

.commands-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
  /* Removed max-height and overflow to let modal body handle scrolling
     and prevent dropdown clipping */
}

/* Ensure dropdowns can overflow the container */
.command-editor {
  overflow: visible;
}

.command-type {
  position: relative;
  z-index: 10;
}

.command-type select {
  position: relative;
}

.commands-empty {
  padding: 20px;
  text-align: center;
  color: var(--text-tertiary);
  font-size: 13px;
}

.command-editor {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid var(--border-color);
  border-radius: var(--radius-sm);
  padding: 12px;
}

.command-header {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
}

.command-index {
  width: 24px;
  height: 24px;
  background: var(--accent-gradient);
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 11px;
  font-weight: 600;
  color: var(--bg-primary);
  flex-shrink: 0;
}

.command-type {
  flex: 1;
  max-width: 200px;
  min-width: 160px;
}

.command-actions {
  display: flex;
  gap: 4px;
  margin-left: auto;
}

.command-params {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.param-row {
  display: flex;
  align-items: center;
  gap: 8px;
  flex-wrap: wrap;
}

.param-row label {
  font-size: 11px;
  color: var(--text-tertiary);
  flex-shrink: 0;
}

.param-row .input {
  flex: 1;
  min-width: 80px;
}

.param-input {
  max-width: 70px;
}
