___CSS_LOADER_EXPORT___.push([module.id, `.rundown-list {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.rundown-list-header {
  display: flex;
  gap: 6px;
  padding: 12px;
  border-bottom: 1px solid var(--border-color);
}

.rundown-list-header .btn-primary,
.rundown-list-header .btn-secondary {
  flex: 1;
  justify-content: center;
}

.rundown-list-header .btn-secondary {
  background: rgba(255, 255, 255, 0.1);
  color: var(--text-secondary);
}

.rundown-list-header .btn-secondary:hover {
  background: rgba(255, 255, 255, 0.15);
  color: var(--text-primary);
}

.rundown-list-header .btn-icon {
  flex-shrink: 0;
}

.rundown-error {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 12px;
  margin: 8px 12px 0;
  background: rgba(255, 68, 68, 0.1);
  border: 1px solid rgba(255, 68, 68, 0.3);
  border-radius: var(--radius-sm);
  color: var(--error-light);
  font-size: 12px;
}

.save-dialog {
  padding: 12px;
  border-bottom: 1px solid var(--border-color);
  background: rgba(255, 255, 255, 0.02);
}

.save-dialog input {
  width: 100%;
  margin-bottom: 8px;
}

.save-dialog-actions {
  display: flex;
  gap: 8px;
  justify-content: flex-end;
}

.rundown-items {
  flex: 1;
  overflow-y: auto;
  padding: 8px;
}

.rundown-empty {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 40px 20px;
  text-align: center;
  color: var(--text-tertiary);
}

.rundown-empty svg {
  margin-bottom: 12px;
}

.rundown-empty p {
  margin: 0 0 4px;
  font-size: 13px;
}

.rundown-empty .text-sm {
  font-size: 11px;
}

.rundown-item {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 10px 12px;
  background: rgba(255, 255, 255, 0.03);
  border-radius: var(--radius-sm);
  margin-bottom: 6px;
  transition: all var(--transition);
}

.rundown-item:hover {
  background: rgba(255, 255, 255, 0.06);
}

.rundown-icon {
  width: 36px;
  height: 36px;
  background: var(--accent-gradient);
  border-radius: 6px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--bg-primary);
  flex-shrink: 0;
}

.rundown-info {
  flex: 1;
  min-width: 0;
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.rundown-name {
  font-size: 13px;
  font-weight: 500;
  color: var(--text-primary);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.rundown-meta {
  font-size: 11px;
  color: var(--text-tertiary);
}

.rundown-actions {
  display: flex;
  gap: 6px;
  flex-shrink: 0;
}

.rundown-actions .btn-sm {
  padding: 5px 10px;
  font-size: 11px;
}

/* Larger delete button for better visibility */
.rundown-actions .btn-danger-icon {
  width: 32px;
  height: 32px;
  padding: 6px;
  border-radius: 6px;
  background: rgba(255, 68, 68, 0.1);
  border: none;
  color: var(--error-light);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all var(--transition);
}

.rundown-actions .btn-danger-icon:hover {
  background: rgba(255, 68, 68, 0.2);
}

.rundown-actions .btn-danger-icon svg {
  width: 16px;
  height: 16px;
}
