___CSS_LOADER_EXPORT___.push([module.id, `.playlist {
  flex: 1;
  min-height: 100px;
  max-height: 200px;
  overflow-y: auto;
  overflow-x: hidden;
  transition: all var(--transition);
}

.playlist.expanded {
  max-height: 400px;
}

.playlist.empty {
  display: flex;
  align-items: center;
  justify-content: center;
}

.playlist.drag-over {
  background: rgba(0, 170, 136, 0.1);
  border-color: var(--accent-primary);
}

.playlist-empty {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  padding: 20px;
  color: var(--text-tertiary);
  text-align: center;
}

.playlist-empty p {
  margin: 0;
  font-size: 12px;
}

.playlist-items {
  display: flex;
  flex-direction: column;
  gap: 2px;
  padding: 8px;
}

.playlist-item-wrapper {
  position: relative;
}

.playlist-item-wrapper.drag-over::before {
  content: '';
  position: absolute;
  top: -1px;
  left: 0;
  right: 0;
  height: 2px;
  background: var(--accent-secondary);
  z-index: 1;
}
