___CSS_LOADER_EXPORT___.push([module.id, `.preview {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  border-radius: var(--radius-sm);
  overflow: hidden;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.preview.expanded {
  min-height: 400px;
}

.preview-video {
  width: 100%;
  height: 100%;
  object-fit: contain;
  background: #000;
}

.preview-placeholder {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 12px;
  padding: 20px;
  text-align: center;
  color: var(--text-tertiary);
}

.preview-placeholder p {
  margin: 0;
  font-size: 14px;
}

.preview-placeholder .btn-sm {
  margin-top: 8px;
}

.preview-buttons {
  display: flex;
  gap: 8px;
  margin-top: 8px;
}

.streaming-badge {
  display: inline-block;
  margin-left: 6px;
  padding: 1px 6px;
  background: var(--error-light);
  color: white;
  font-size: 9px;
  font-weight: 600;
  border-radius: 3px;
  animation: blink 1.5s ease-in-out infinite;
}

@keyframes blink {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.6; }
}

.preview-controls {
  position: absolute;
  bottom: 8px;
  left: 8px;
  right: 8px;
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 6px 10px;
  background: rgba(0, 0, 0, 0.7);
  border-radius: var(--radius-sm);
  opacity: 0;
  transition: opacity var(--transition);
}

.preview:hover .preview-controls {
  opacity: 1;
}

.preview-label {
  font-size: 11px;
  color: var(--text-secondary);
  margin-left: auto;
}

.btn-sm {
  padding: 6px 12px;
  font-size: 12px;
}

/* Loading spinner for connecting state */
.preview-loading {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 8px;
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 3px solid var(--border-color);
  border-top-color: var(--accent-primary);
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
