___CSS_LOADER_EXPORT___.push([module.id, `.media-preview {
  display: flex;
  flex-direction: column;
  gap: 12px;
  padding: 12px;
  background: rgba(255, 255, 255, 0.02);
  border-radius: var(--radius-sm);
  border: 1px solid var(--border-color);
}

.media-preview.empty {
  align-items: center;
  justify-content: center;
  min-height: 200px;
  color: var(--text-tertiary);
  text-align: center;
}

.media-preview.empty p {
  margin: 8px 0 0;
  font-size: 12px;
}

.preview-thumbnail {
  position: relative;
  width: 100%;
  aspect-ratio: 16 / 9;
  background: rgba(0, 0, 0, 0.3);
  border-radius: var(--radius-sm);
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.preview-thumbnail img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.preview-placeholder {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  color: var(--text-tertiary);
}

.preview-loading {
  display: flex;
  align-items: center;
  justify-content: center;
}

.loading-spinner {
  width: 24px;
  height: 24px;
  border: 2px solid rgba(255, 255, 255, 0.1);
  border-top-color: var(--accent-primary);
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.preview-info {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.preview-name {
  margin: 0;
  font-size: 13px;
  font-weight: 600;
  color: var(--text-primary);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.preview-details {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.detail-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
}

.detail-label {
  color: var(--text-tertiary);
}

.detail-value {
  color: var(--text-secondary);
  text-align: right;
}

.type-badge {
  padding: 2px 8px;
  border-radius: 4px;
  font-size: 10px;
  font-weight: 600;
  text-transform: uppercase;
}

.type-badge[data-type="video"] {
  background: rgba(0, 184, 148, 0.2);
  color: #00b894;
}

.type-badge[data-type="image"] {
  background: rgba(225, 112, 85, 0.2);
  color: #e17055;
}

.type-badge[data-type="audio"] {
  background: rgba(108, 92, 231, 0.2);
  color: #6c5ce7;
}

.type-badge[data-type="template"] {
  background: rgba(0, 206, 201, 0.2);
  color: #00cec9;
}

.preview-path {
  font-size: 11px;
  color: var(--text-tertiary);
  word-break: break-all;
  padding-top: 8px;
  border-top: 1px solid var(--border-color);
}
