___CSS_LOADER_EXPORT___.push([module.id, `.channel {
  background: rgba(255, 255, 255, 0.03);
  backdrop-filter: blur(10px);
  border: 1px solid var(--border-color);
  border-radius: var(--radius-lg);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  position: relative;
  transition: width 300ms ease;
}

.channel-header {
  padding: 16px;
  border-bottom: 1px solid var(--border-color);
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: rgba(255, 255, 255, 0.02);
}

.channel-name {
  font-size: 15px;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0;
}

.channel-actions {
  display: flex;
  gap: 8px;
}

.btn-danger-icon:hover {
  background: rgba(255, 68, 68, 0.2);
  color: var(--error-light);
}

.channel-preview {
  width: 420px;
  height: 240px;
  background: rgba(0, 0, 0, 0.3);
  border-bottom: 1px solid var(--border-color);
  flex-shrink: 0;
}

.preview-placeholder {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 12px;
  color: var(--text-tertiary);
}

.preview-placeholder p {
  margin: 0;
  font-size: 14px;
}

.channel-layers {
  display: flex;
  gap: 12px;
  padding: 16px;
  overflow-x: auto;
  flex: 1;
}

.layer-placeholder {
  width: 380px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid var(--border-color);
  border-radius: var(--radius-md);
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
}

.layer-header {
  padding: 12px;
  border-bottom: 1px solid var(--border-color);
  font-size: 13px;
  font-weight: 600;
  color: var(--text-secondary);
}

.layer-content {
  flex: 1;
  padding: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.add-layer-button {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 2px dashed var(--accent-primary);
  background: rgba(0, 255, 136, 0.05);
  color: var(--accent-secondary);
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all var(--transition);
  align-self: center;
  flex-shrink: 0;
}

.add-layer-button:hover {
  transform: scale(1.1);
  background: rgba(0, 255, 136, 0.1);
  box-shadow: var(--shadow-glow);
}

.add-layer-button:active {
  transform: scale(1.05);
}

/* Delete Confirmation */
.delete-confirm-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10;
  animation: fadeIn 0.2s ease;
}

.delete-confirm {
  background: linear-gradient(135deg, rgba(26, 26, 46, 0.98) 0%, rgba(42, 42, 62, 0.98) 100%);
  border: 1px solid var(--border-color);
  border-radius: var(--radius-md);
  padding: 24px;
  max-width: 400px;
  text-align: center;
}

.delete-confirm h3 {
  margin: 0 0 12px 0;
  font-size: 18px;
  font-weight: 600;
  color: var(--text-primary);
}

.delete-confirm p {
  margin: 0 0 20px 0;
  font-size: 13px;
  color: var(--text-secondary);
  line-height: 1.5;
}

.delete-confirm-buttons {
  display: flex;
  gap: 12px;
  justify-content: center;
}`, "",{"version":3,"sources":["webpack://./src/renderer/components/Channel.css"],"names":[],"mappings":"AAAA;EACE,qCAAqC;EACrC,2BAA2B;EAC3B,qCAAqC;EACrC,+BAA+B;EAC/B,gBAAgB;EAChB,aAAa;EACb,sBAAsB;EACtB,cAAc;EACd,kBAAkB;EAClB,4BAA4B;AAC9B;;AAEA;EACE,aAAa;EACb,4CAA4C;EAC5C,aAAa;EACb,mBAAmB;EACnB,8BAA8B;EAC9B,qCAAqC;AACvC;;AAEA;EACE,eAAe;EACf,gBAAgB;EAChB,0BAA0B;EAC1B,SAAS;AACX;;AAEA;EACE,aAAa;EACb,QAAQ;AACV;;AAEA;EACE,kCAAkC;EAClC,yBAAyB;AAC3B;;AAEA;EACE,YAAY;EACZ,aAAa;EACb,8BAA8B;EAC9B,4CAA4C;EAC5C,cAAc;AAChB;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,aAAa;EACb,sBAAsB;EACtB,mBAAmB;EACnB,uBAAuB;EACvB,SAAS;EACT,2BAA2B;AAC7B;;AAEA;EACE,SAAS;EACT,eAAe;AACjB;;AAEA;EACE,aAAa;EACb,SAAS;EACT,aAAa;EACb,gBAAgB;EAChB,OAAO;AACT;;AAEA;EACE,YAAY;EACZ,qCAAqC;EACrC,qCAAqC;EACrC,+BAA+B;EAC/B,aAAa;EACb,sBAAsB;EACtB,cAAc;AAChB;;AAEA;EACE,aAAa;EACb,4CAA4C;EAC5C,eAAe;EACf,gBAAgB;EAChB,4BAA4B;AAC9B;;AAEA;EACE,OAAO;EACP,aAAa;EACb,aAAa;EACb,mBAAmB;EACnB,uBAAuB;AACzB;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,kBAAkB;EAClB,wCAAwC;EACxC,mCAAmC;EACnC,8BAA8B;EAC9B,eAAe;EACf,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,iCAAiC;EACjC,kBAAkB;EAClB,cAAc;AAChB;;AAEA;EACE,qBAAqB;EACrB,kCAAkC;EAClC,8BAA8B;AAChC;;AAEA;EACE,sBAAsB;AACxB;;AAEA,wBAAwB;AACxB;EACE,kBAAkB;EAClB,MAAM;EACN,OAAO;EACP,WAAW;EACX,YAAY;EACZ,8BAA8B;EAC9B,0BAA0B;EAC1B,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,WAAW;EACX,2BAA2B;AAC7B;;AAEA;EACE,2FAA2F;EAC3F,qCAAqC;EACrC,+BAA+B;EAC/B,aAAa;EACb,gBAAgB;EAChB,kBAAkB;AACpB;;AAEA;EACE,kBAAkB;EAClB,eAAe;EACf,gBAAgB;EAChB,0BAA0B;AAC5B;;AAEA;EACE,kBAAkB;EAClB,eAAe;EACf,4BAA4B;EAC5B,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,SAAS;EACT,uBAAuB;AACzB","sourcesContent":[".channel {\r\n  background: rgba(255, 255, 255, 0.03);\r\n  backdrop-filter: blur(10px);\r\n  border: 1px solid var(--border-color);\r\n  border-radius: var(--radius-lg);\r\n  overflow: hidden;\r\n  display: flex;\r\n  flex-direction: column;\r\n  flex-shrink: 0;\r\n  position: relative;\r\n  transition: width 300ms ease;\r\n}\r\n\r\n.channel-header {\r\n  padding: 16px;\r\n  bord