___CSS_LOADER_EXPORT___.push([module.id, `.sidebar {
  width: 380px;
  background: rgba(255, 255, 255, 0.02);
  border-right: 1px solid var(--border-color);
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
}

.sidebar-tabs {
  display: flex;
  border-bottom: 1px solid var(--border-color);
  background: rgba(255, 255, 255, 0.03);
}

.sidebar-tab {
  flex: 1;
  padding: 12px 16px;
  background: none;
  border: none;
  color: var(--text-secondary);
  font-size: 13px;
  font-weight: 500;
  cursor: pointer;
  transition: all var(--transition);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  border-bottom: 2px solid transparent;
}

.sidebar-tab:hover {
  color: var(--text-primary);
  background: rgba(255, 255, 255, 0.05);
}

.sidebar-tab.active {
  color: var(--accent-secondary);
  border-bottom-color: var(--accent-secondary);
  background: rgba(0, 255, 136, 0.05);
}

.sidebar-tab svg {
  width: 16px;
  height: 16px;
}

.sidebar-content {
  flex: 1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}`, "",{"version":3,"sources":["webpack://./src/renderer/components/Sidebar.css"],"names":[],"mappings":"AAAA;EACE,YAAY;EACZ,qCAAqC;EACrC,2CAA2C;EAC3C,aAAa;EACb,sBAAsB;EACtB,cAAc;AAChB;;AAEA;EACE,aAAa;EACb,4CAA4C;EAC5C,qCAAqC;AACvC;;AAEA;EACE,OAAO;EACP,kBAAkB;EAClB,gBAAgB;EAChB,YAAY;EACZ,4BAA4B;EAC5B,eAAe;EACf,gBAAgB;EAChB,eAAe;EACf,iCAAiC;EACjC,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,QAAQ;EACR,oCAAoC;AACtC;;AAEA;EACE,0BAA0B;EAC1B,qCAAqC;AACvC;;AAEA;EACE,8BAA8B;EAC9B,4CAA4C;EAC5C,mCAAmC;AACrC;;AAEA;EACE,WAAW;EACX,YAAY;AACd;;AAEA;EACE,OAAO;EACP,gBAAgB;EAChB,aAAa;EACb,sBAAsB;AACxB","sourcesContent":[".sidebar {\r\n  width: 380px;\r\n  background: rgba(255, 255, 255, 0.02);\r\n  border-right: 1px solid var(--border-color);\r\n  display: flex;\r\n  flex-direction: column;\r\n  flex-shrink: 0;\r\n}\r\n\r\n.sidebar-tabs {\r\n  display: flex;\r\n  border-bottom: 1px solid var(--border-color);\r\n  background: rgba(255, 255, 255, 0.03);\r\n}\r\n\r\n.sidebar-tab {\r\n  flex: 1;\r\n  padding: 12px 16px;\r\n  background: none;\r\n  border: none;\r\n  color: var(--text-secondary);\r\n  font-size: 13px;\r\n  font-weight: 500;\r\n  cursor: pointer;\r\n  transition: all var(--transition);\r\n  display: flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  gap: 8px;\r\n  border-bottom: 2px solid transparent;\r\n}\r\n\r\n.sidebar-tab:hover {\r\n  color: var(--text-primary);\r\n  background: rgba(255, 255, 255, 0.05);\r\n}\r\n\r\n.sidebar-tab.active {\r\n  color: var(--accent-secondary);\r\n  border-bottom-color: var(--accent-secondary);\r\n  background: rgba(0, 255, 136, 0.05);\r\n}\r\n\r\n.sidebar-tab svg {\r\n  width: 16px;\r\n  height: 16px;\r\n}\r\n\r\n.sidebar-content {\r\n  flex: 1;\r\n  overflow: hidden;\r\n  display: flex;\r\n  flex-direction: column;\r\n}"],"sourceRoot":""}]);
